
.shopcart{position: fixed;left: 0;bottom: 0;z-index: 88;width: 100%;height: 48px;
}
.shopcart .shopcart_content{font-size: 0;display: -webkit-box;display: -ms-flexbox;display: flex;background-color: #141d27;color:rgba(255,255,255,.4)
}
.shopcart_content .content_left{-webkit-box-flex: 1;-ms-flex: 1;flex: 1
}
.shopcart_content .content_right{-webkit-box-flex: 0;-ms-flex: 0 0 105px;flex: 0 0 105px;width: 105px
}
.content_left .logo_wrapper{display: inline-block;position: relative;top: -10px;margin: 0 12px;padding: 6px;width: 56px;height: 56px;-webkit-box-sizing:border-box;box-sizing:border-box;vertical-align: top;border-radius: 50%;background-color: #141d27
}
.content_left .logo_wrapper .logo{width: 100%;height: 100%;border-radius: 50%;background-color: #2b343c;text-align: center;
}
.content_left .logo_wrapper .logo .icon-shopping_cart{font-size: 24px;color: #80858a;line-height: 44px
}
.logo_wrapper .num{position: absolute;right: 0;top: 0;width: 24px;height: 16px;line-height: 16px;text-align: center;border-radius: 16px;font-size: 9px;font-weight: 700;color: #FFF;background-color: rgb(240,20,20);-webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,.4);box-shadow: 0 4px 8px 0 rgba(0,0,0,.4)
}
.content_left .price{display: inline-block;vertical-align: top;line-height: 24px;padding-top: 12px;-webkit-box-sizing:border-box;box-sizing:border-box;padding-right:12px;border-right: 1px solid rgba(255,255,255,.1);font-size: 14px;font-weight: 700;color: rgba(255,255,255,.4);
}
.content_left .desc{display: inline-block;line-height: 24px;vertical-align: top;padding: 12px 0 0 12px;font-size: 10px;color:rgba(255,255,255,.4); -webkit-box-sizing:border-box; box-sizing:border-box;
}
/*高亮css*/
.content_right .pay{font-size: 12px;height: 48px;line-height: 48px;text-align: center;
}
.content_right .notEnough{background-color: #2b333b
}
.content_right .enough{background-color: #00b43c;color: #fff
}
/*抛物线*/
.ball_container .balls{position: fixed;left: 32px;bottom:22px;z-index: 188;-webkit-transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41); /*贝塞尔曲线*/
}
.ball_container .ball .ballinner{width: 16px;height: 16px;border-radius: 50%;background-color: rgb(0,160,220);-webkit-transition: all 0.4s linear;transition: all 0.4s linear;
}
/*购物车列表*/
.shopcart-list{position: absolute;top: 0;left: 0;z-index: -1;width: 100%;-webkit-transform: translate3d(0,-100%,0);transform: translate3d(0,-100%,0)
}
.shopcart-list .list-header{height: 40px;line-height: 40px;padding:0 18px;background-color: #f3f5f7;border-bottom: 1px solid rgba(7,17,27,.1)
}
.shopcart-list .list-header .title{float: left;font-size: 14px;color: rgb(7,17,27)
}
.shopcart-list .list-header .empty{float: right;font-size: 12px;color: rgb(0,160,220)
}
/*购物车详情页*/
.shopcart-list .list-content{padding: 0 18px;max-height: 217px;background-color: #fff;overflow: hidden;
}
.shopcart-list .list-content .food{position: relative;padding: 12px 0;-webkit-box-sizing:boder-box;box-sizing:boder-box;border-bottom: 1px solid rgba(7,17,27,.1)
}
.shopcart-list .list-content .name{line-height: 24px;font-size: 14px;color:rgb(7,17,27);
}
.shopcart-list .list-content .price{position: absolute;right: 90px;bottom: 12px;line-height: 24px;font-size: 14px;font-weight: 700;color: rgb(240,20,20)
}
.cartcontrol-wrapper{position: absolute;right: 0;bottom: 6px;
}
/*购物车列表CSS3*/
.cartfold-enter-active,.cartfold-leave-active{-webkit-transition: all .4s linear;transition:all .4s linear;
}
.cartfold-enter,.cartfold-leave-to{-webkit-transform: translate3d(0,0,0);transform: translate3d(0,0,0)
}	
/*模态*/
.list_mask{position: fixed;top: 0;left: 0;z-index: 40;width: 100%;height: 100%;background-color: rgba(7,17,27,.6);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);
}
.modelfade-enter-active,.modelfade-leave-active{-webkit-transition: all .4s linear;transition:all .4s linear;
}
.modelfade-enter,.modelfade-leave-to{background-color: rgba(7,17,27,0);
}		



